	export default {
		data() {
			return {
				path:'pages/service/detail',
				tabBars: [
					{name:'服务介绍',id:1},
					{name:'服务保障',id:2},
					{name:'关于我们',id:3},
				], //知识分类
				scrollLeft: 0,
				tabIndex: 0,
				symbolId:'',
				spellGroup:false,
				specificat:false,
				specificatbuy:false,
				specificatbuycan:false,
				biId:'',
				authKey:'',
				content:'',
				carousel:'',
				skus:'',
				assemble_skus:'',
				price_minA:'',
				price_minN:'',
				prices:'',
				priceA:'',
				buyTimes:'',
				specif:'',
				specG:'',
				evaluate:'',
				number:'',
				count:'',
				assemble:'',
				normal:'',
				group:'',
				index:-1,
				agindex:-1,
				azindex:-1,
				bsku_id:'',
				cassemble_id:'',
				invite_code:'',
				bassemble_skus:'',
				assembles:'',
				coupon_id:'',
				contentText:'',
				code:'',
				normalcan:'',
				showVideo: true,
				share_title: '',
				user: false,
				userInfo: ''
			};
		},
		onLoad(e) {
			this.userInfo = uni.getStorageSync('userInfo');
			this.authKey = uni.getStorageSync('authKey')
			if (this.authKey){
				this.checkSession()
			}
			this.biId = e.id;
			console.log(e)
			this.invite_code = e.invite_code;
			// 处理判断sence
			if(e.scene){
				const reg = /%26/g
				if(reg.test(e.scene)){
					// 有两个参数
					const arr = e.scene.split('%26')
					this.biId = arr[0]
					this.invite_code = arr[1]
				}else{
					// 有一个参数
					this.biId = e.scene
				}
				
				
			}
			this.getListDetail();
			this.getListComments();
			this.gofight();
		},
		
		onShareAppMessage(res) {
			// if (this.authKey == ''){
			// 	this.user = true
			// 	return
			// }
		    if (res.from === 'button') {
		       // 来自页面内转发按钮
		       console.log(this.code)
		    }
		    return {
		       title: this.share_title,
		       path: "/pages/service/detail?id=" + this.biId + "&invite_code=" + this.code,
		       imageUrl: this.content.cover,
			}
		},
		computed: {
            calHeight() {
				if (this.assembles){
					let len = this.assembles.length
					return uni.upx2px(len * 120) + 'px';	
				}
				return '0px'
            },
			csene(){
				return this.biId + '&' + this.code
			}
        },
		methods:{
			// 检查会话是否失效
		checkSession: function() {
				var that = this;
				uni.request({
					url: this.http + 'user/checkSession',
					method: 'POST',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						if (res.data.code == 101) {
							that.authKey = ''
							uni.setStorageSync('authKey','')
						}
					},
				});
		},
		wxGetUserInfo:function(res){
			let that = this
			if (!res.detail.iv) {
				uni.showToast({ 
					title: "您取消了授权,登录失败", 
					icon: "none" ,
				}); 
				return false; 
			}
			let simpleUser = res.detail.userInfo
			let encryptedData = res.detail.encryptedData
			let iv = res.detail.iv
			uni.login({
				provider: 'weixin',
			    success(res){
				  console.log(res.code,'login')
				  var data = res.code
			    if (res.code) {
				  uni.request({
				  	url: that.http + 'user/token',
				  	method: 'GET',
				  	data: {
				  		login_code: res.code,
						encrypted_data:encryptedData,
						iv: iv
				  	},
				  	success: res => {
						if (res.data.code == 200) {
							let auth = res.data.data.authkey
							that.authKey = auth
							that.userInfo = simpleUser
							uni.setStorageSync('authKey',auth)
							uni.setStorageSync('userInfo',simpleUser)
							that.user = false
							
							that.gofight()
						} else {
							uni.showToast({
								title: res.data.error,
								icon: 'none'
							})
						}
				  		
				  		// that.haveOrder()
				  	},
				  });
			    } else {
			      console.log('登录失败！' + res.errMsg)
			    }
			  }
			})
		},
			close(e){
				this.user = false;
				uni.showToast({
					title: '未微信授权',
					icon:'none',
					mask: false,
					duration: 1500
				})
			},
			// 邀请码
			gofight(){
				uni.request({
					url: this.http+'home/profile',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
					},
					success: res => {
						if (res.data.code == 200) {
							this.code = res.data.data.invite_code
						}
					},
				});
			},
			getElSize(id) { //得到元素的size
			console.log(id)
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data)
					}).exec()
				})
			},
			async tapTab(e) { //点击tab-bar
				this.symbolId = e.currentTarget.id
				console.log(this.symbolId)
				if (this.tabIndex === e.target.dataset.current) {
					return false;
				} else {
					let tabBar = await this.getElSize("tab-bar"),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = e.target.dataset.current;
				}
				// this.requestLearnList()
			},
			// 获取服务详情
			getListDetail(){
				var that = this;
				uni.request({
					url: this.http+'service/detail/' + this.biId,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						console.log(res.data)
						let that = this
						if (res.data.code == 200) {
							this.content = res.data.data
							this.content.video = this.content.vedio
							this.content.vedio = this.static + this.content.vedio
							this.content.cover = this.static + this.content.cover
							this.share_title = this.content.share_title
							this.price_minA = res.data.data.price.assemble
							this.price_minN = res.data.data.price.normal
							this.prices = res.data.data.price.normal.price_min
							this.priceA =  res.data.data.price.assemble.price_min
							this.carousel = res.data.data.carousel
							if (this.carousel){
								for (let index in this.carousel) {
									this.carousel[index] = that.static + this.carousel[index]
								}
							}
							this.assemble = res.data.data.price.assemble.items
							this.normal = res.data.data.price.normal.items
							this.group = res.data.data.price.assemble.items[0]
							this.assembles = res.data.data.assembles
							console.log(this.assembles)
							this.contentText = res.data.data.content
						}
					},
				});
			},
			// 获取评价
			getListComments(){
				var that = this;
				uni.request({
					url: this.http+'comment/overview/' + this.biId,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							this.evaluate = res.data.data.present
							this.number = res.data.data
							this.count = res.data.data.count
						}
					},
				});
			},
			// 回首页
			home(){
			   uni.reLaunch({
			   	  url: '/pages/knowledge/knowledge'
			   });
			},
			
			// 评价页面
			goevaluat(){
				uni.navigateTo({
					url: '/pages/mine/evaluate/viewRatings?id=' + this.biId
				});
			},
			// 参加拼团
			goGroupList(item){
				if (this.authKey == ''){
					this.user = true
					return
				}
				this.cassemble_id = item.id
				uni.request({
					url: this.http+'assemble/detail/' + item.id,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							this.normalcan = res.data.data.skus.items
							this.specificatbuycan = true
					    }else{
							uni.showToast({
								title:res.data.error,
								icon:'none'
							})
						}
					}
				});
			},
			// 选择规格
			 chooseCan(item,idx){
				 if(item.available == 0){
					 uni.showToast({
					 	title:'体验价不可使用',
					 	icon:'none',
					 	duration:1500
					 })
				 }else{
					 console.log(item)
					 this.index = idx
					 this.buyTimes = item.name + '(' + item.times + '次)'
					 this.prices = item.prices.final_price
					 this.bsku_id = item.id
				 }
			},
		     choose(item,idx){
				 if(item.available == 0){
					 uni.showToast({
					 	title:'体验价不可使用',
					 	icon:'none',
					 	duration:1500
					 })
				 }else{
					 this.index = idx
					 this.buyTimes = item.name + '(' + item.times + '次)'
					 this.prices = item.price
					 this.bsku_id = item.id
				 }
			},
			chooseGroup(item,ii){
				this.agindex = ii
				this.specif = item.name + '(' + item.times + '次)'
				console.log(item)
				this.priceA = item.price
				this.group = item
				this.bsku_id = item.id
				this.bassemble_skus = ''
				this.specG = ""
				this.azindex = -1
			},
			Groupnum(item,iz){
				this.azindex = iz;
				this.priceA = item.final_price
				this.specG = item.name
				this.bassemble_skus = item.id
				// console.log(this.bassemble_skus)
			},
			// 查看更多拼团
			gomore(){
				this.spellGroup = true;
			},
			closeGroup(){
				this.spellGroup = false;
			},
			// 直接购买
			buyDirect(){
				if (this.authKey == ''){
					this.user = true
					return
				}
				this.specificatbuy = true;
				this.showVideo = false
			},
			sureBuyDirect(){
				this.specificatbuy = false;
				this.specificatbuycan = false
			},
			// 直接购买   参与拼团购买
			gobuy(){
				if(this.bsku_id == ''){
					uni.showToast({
						title:'请选择商品规格',
						icon:'none',
						duration:1500
					})
					return
				}
				this.spellGroup = false;
				var that = this;
				uni.request({
					url: this.http+'order/preview',
					method: 'POST',
					header: {
						authKey: this.authKey
					},
					data: {
						product_id:this.biId,
						sku_id:this.bsku_id,
						assemble_id:this.cassemble_id,
						coupon_id:this.coupon_id,
						invite_code:this.invite_code
					},
					success: res => {
						console.log(res)
						if (res.data.code == 200) {
							let data = res.data.data
							data.item.product.cover = this.static + data.item.product.cover
							uni.navigateTo({
								url: '/pages/order/confirmOrder?data=' + encodeURIComponent(JSON.stringify(data)),
							});
						}else{
							uni.showToast({
								title:res.data.error,
								icon:"none"
							})
						}
					},
				});
			},
			// 发起拼团购买
			spellgroupbuy(){
				if(this.bsku_id == ""){
					uni.showToast({
						title:'请选择商品规格',
						icon:'none',
						duration:1500
					})
					return
				}
				if(this.bassemble_skus == ""){
					uni.showToast({
						title:'请选择拼团人数',
						icon:'none',
						duration:1500
					})
					return
				}
				var that = this;
				uni.request({
					url: this.http+'assemble/preview',
					method: 'POST',
					header: {
						authKey: this.authKey
					},
					data: {
						product_id:this.biId,
						sku_id:this.bsku_id,
						category_id:this.bassemble_skus
					},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							let order = res.data.data
							order.product.cover = that.static + order.product.cover
							uni.navigateTo({
								url: '/pages/order/spellconfirmOrder?data=' + encodeURIComponent(JSON.stringify(order)),
							});
						}else if(res.data.code == 101){
							this.user = true
						}
					},
				});
			},
			// 拼团购买
			buy(){
				// if (this.authKey == ''){
				// 	this.user = true
				// 	return
				// }
				this.specificat = true;
				this.showVideo = false
			},
			// 关闭购买
			goclose(){
				this.specificat = false;
				this.specificatbuy = false;
				this.specificatbuycan = false;
				this.cassemble_id = ''
				this.showVideo = true
			},
		}
	}
